  data () {
    return {
      classifyActive: ['1', '2'],
      modalSelected: false,
      marketOptions: [{
        value: '版权品',
        label: '版权品'
      }],
      themeOptions: [{
        value: '元计划',
        label: '元计划'
      }],
      moduleOptions: [{
        value: '元计划·南',
        label: '元计划·南'
      }, {
