      sortOptions: [{
        value: '价格从高到低',
        label: '价格从高到低'
      }, {
        value: '价格从低到高',
        label: '价格从低到高'
      }, {
        value: '按照时间排序',
        label: '按照时间排序'
      }],
      optionsObj: {},
      selectedOptions: []
    }
  },
  methods: {
    onModelSelect (activeIdx) {
      this.modalSelected = this.modalSelected === activeIdx ? false : activeIdx
    },
    onOptionSelect (title, options) {
      this.optionsObj[title] = options

      let selectedOptions = []
      Object.values(this.optionsObj).forEach(itm => {
        selectedOptions = [...selectedOptions, ...itm]
      })
      this.selectedOptions = selectedOptions
    }
  }
}
